import React from 'react'
import { Form, Formik } from 'formik'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'

import Button from 'common/components/button'
import { LinkButton } from 'common/components'
import { FreeTextField, ReactSelectField } from 'common/components/form'

interface AddAnnotationFormProps {
  categories: any
  onSubmit(object): any
}

const AddAnnotationForm = ({ categories, onSubmit }: AddAnnotationFormProps) => {
  const [isNotesVisible, setIsNotesVisible] = React.useState(false)

  return (
    <Formik
      initialValues={{
        category: null,
        notes: ''
      }}
      onSubmit={values => {
        const { category, notes } = values
        onSubmit({ id: category.id, notes })
      }}
      render={({ handleChange, setFieldValue, touched, values, isSubmitting }) => (
        <Form>
          <ReactSelectField
            name='category'
            placeholder='Select category'
            value={values.category}
            onChange={category => setFieldValue('category', category)}
            data-cy='add-annotation--field'
            selectProps={{
              autoFocus: true,
              getOptionLabel: opt => opt.name,
              filterOption: ({ data }, str) => data.name && data.name.includes(str)
            }}
            containerProps={{
              'data-cy': 'add-annotation--select'
            }}
            options={categories}
          />

          {isNotesVisible && (
            <FreeTextField
              name='notes'
              label='Notes'
              onChange={handleChange}
              touched={touched.notes}
              value={values.notes}
              isOptional={true}
            />
          )}

          <p className='tr'>
            {isNotesVisible ? (
              <LinkButton onClick={() => setIsNotesVisible(false)}>
                Hide notes
              </LinkButton>
            ) : (
              <LinkButton onClick={() => setIsNotesVisible(true)}>Add notes</LinkButton>
            )}
          </p>

          <Button className='mv2' type='submit' disabled={isSubmitting}>
            {isSubmitting ? 'Submitting...' : 'Add annotation'}
          </Button>
        </Form>
      )}
    />
  )
}

interface AddAnnotationPopupProps {
  categories: any
  onSave(any): any
  onCancel(any): any
  placement: any
  style: React.CSSProperties
}

const AddAnnotationPopup = React.forwardRef(
  (
    { categories, onSave, onCancel, placement, style }: AddAnnotationPopupProps,
    ref: React.Ref<HTMLDivElement>
  ) => (
    <div
      ref={ref}
      style={style}
      data-placement={placement}
      data-cy='add-annotation'
      className='bg-white pv3 ph3 mt2 nowrap w6 ba b--gray shadow-3'
      onMouseUp={e => e.stopPropagation()}
    >
      <div className='flex flex-row space-between justify-between'>
        <h4 className='mt0 flex-1'>Create annotation</h4>

        <div className='pointer flex-0' onClick={onCancel}>
          <FontAwesomeIcon icon={faTimes} size='lg' />
        </div>
      </div>
      <AddAnnotationForm categories={categories} onSubmit={onSave} />
    </div>
  )
)

export default AddAnnotationPopup
