import React from 'react'
import { Link } from '@reach/router'
import classNames from 'classnames'

import { tachyonsMerge } from './index'

export const sharedButtonClasses =
  'no-underline items-center ph3 pv2 btn white br1 ba bw1'

export const classesForLevel = level => {
  return `btn-${level}`
}

const Button = React.forwardRef((props, ref) => {
  const { children, className, level = 'primary', ...restProps } = props
  const classes = tachyonsMerge(
    classNames([
      sharedButtonClasses,
      'pointer',
      classesForLevel(level),
      restProps.disabled && 'disabled'
    ]),
    className
  )

  return (
    <button ref={ref} className={classes} {...restProps}>
      {children}
    </button>
  )
})

export const LinkButton = ({ className, level = 'primary', ...props }) => {
  const classes = tachyonsMerge(
    classNames([
      sharedButtonClasses,
      'ba1 b--blue',
      props.className,
      classesForLevel(level),
      props.disabled && 'disabled'
    ]),
    className
  )

  return <Link getProps={() => ({ className: classes })} {...props} />
}

export default Button
