import React from 'react'
import ApolloClient from 'apollo-boost'
import { ApolloProvider, useQuery, useMutation } from '@apollo/react-hooks'

import 'tachyons/css/tachyons.css'
import 'client/src/app.css'

import {
  NEXT_INCOMPLETE_TASK_QUERY,
  SKIP_TASK_MUTATION,
  CREATE_LABEL_MUTATION
} from 'client/src/annotator/queries'

import TaskProgressBar from 'client/src/annotator/components/task-progress-bar'
import { LabelInterface } from 'client/src/annotator/components/label-interface'

import Button from 'common/components/button'

const App = ({ metadata, ...props }) => {
  // recreate the client when app mounts as a way of clearing the cache and ensure we load latest data from api
  const client = new ApolloClient({
    uri: `${metadata.apiUrl}/graphql?api_key=${metadata.apiKey}`
  })

  return (
    <ApolloProvider client={client}>
      <AnnotationContainer
        metadata={metadata}
        {...props}
      />
    </ApolloProvider>
  )
}

const AnnotationContainer = (props) => {
  const { metadata, onClose } = props
  const { projectId, cycleId, externalDocumentIds } = metadata

  const nextTaskVariables = { projectId, cycleId, externalDocumentIds }

  const { data, loading, error } = useQuery(NEXT_INCOMPLETE_TASK_QUERY, {
    variables: nextTaskVariables
  })

  const [createLabel] = useMutation(CREATE_LABEL_MUTATION, {
    refetchQueries: [{ query: NEXT_INCOMPLETE_TASK_QUERY, variables: nextTaskVariables }]
  })

  const [skipTask] = useMutation(SKIP_TASK_MUTATION, {
    refetchQueries: [{ query: NEXT_INCOMPLETE_TASK_QUERY, variables: nextTaskVariables }]
  })

  const handleClose = (skipConfirm = false) => {
    if (skipConfirm || confirm('Are you sure you want to close the labeler?')) {
      onClose()
    }
  }

  if (loading) { return <div>loading...</div> }
  if (error) {
    console.error(error)
    return <div>There was a problem loading the widget...</div>
  }

  const project = data.projectById
  const task = data.nextIncompleteTask.nodes[0]
  const progress = data.taskProgress

  if (!task) {
    return (
      <div className='flex-1 pa4 tc markers-widget'>
        <h4 className='mb5'>There are no more documents to label.</h4>
        <Button level='secondary' className='inverted' onClick={() => handleClose(true)}>Close labeling widget</Button>
      </div>
    )
  }

  const document = task.document

  // TODO-3: remove currentUserProfile, instead use current_user_id() in a postgres function
  const { currentUserProfile } = data
  const { id: creatorId } = currentUserProfile

  // TODO-3: resolve duplication of handleCreateLabel, handleCreateLabel, handleSkipTask from annotator/index.tsx
  const handleCreateLabel = async (values) => {
    await createLabel({
      variables: {
        projectId,
        cycleId: task.cycleId,
        creatorId,
        taskId: task.id,
        ...values
      }
    })
  }

  const handleCreateReviewLabel = async (values) => {
    const { documentId } = values

    const contents = {
      accept: { result: 'accept' },
      reject: { result: 'reject' },
      modify: values.contents
    }[values.contents.result]

    await createLabel({
      variables: {
        projectId,
        cycleId: task.cycleId,
        creatorId,
        taskId: task.id,
        documentId,
        taskAction: 'review',
        parentLabelId: task.externalLabel.id,
        contents: contents
      }
    })
  }

  const handleSkipTask = async () => {
    await skipTask({ variables: { taskId: task.id } })
  }

  return (
    <div className='markers-widget'>
      <LabelInterface
        {...props}
        project={project}
        task={task}
        document={document}
        progress={progress}
        onSkipTask={handleSkipTask}
        createLabel={handleCreateLabel}
        createReviewLabel={handleCreateReviewLabel}
        sidebarTop={() => (
          <TaskProgressBar
            compact
            complete={progress.complete + progress.skipped}
            total={progress.total}
          />
        )}
      />
    </div>
  )
}

export default App
