import React from 'react'
import { Link as RRLink } from '@reach/router'
import classNames from 'classnames'

export const PageHeader = ({ className = null, title, children }) => {
  const classes = classNames(['mb3 pb1 bb flex items-center', className])

  return (
    <div className={classes}>
      <h3 className='mt0 f5 pb0 mb0 flex-1'>{title}</h3>
      {children}
    </div>
  )
}

export const TableHeader = ({ children }) => (
  <thead>
    <tr>{children}</tr>
  </thead>
)

export const HeaderCell = ({
  className,
  noPadding = false,
  secondary = false,
  ...restProps
}) => (
  <th
    className={classNames([
      'tl pv2',
      !noPadding && 'ph3',
      secondary ? 'normal' : 'ttu f6',
      className
    ])}
    {...restProps}
  />
)

export const TableBody = ({ children }) => <tbody>{children}</tbody>

export const Row = ({ isSelected = false, children }) => (
  <tr
    className={classNames([
      'bt b--black-10 hover-bg-washed-blue',
      isSelected && 'bg-washed-yellow'
    ])}
  >
    {children}
  </tr>
)

export const tachyonsMerge = (defaults, overrides) => {
  // TODO-3: find a better way or figure out how to memoize, this is inefficient to run at every render
  if (!overrides) {
    return defaults
  }

  const classesByFamily = {
    pv: 'pv1 pv2 pv3'.split(' '),
    ph: 'ph1 ph2 ph3'.split(' '),
    mb: 'mb1 mb2 mb3'.split(' '),
    color: 'black blue white'.split(' ')
  }

  const defaultClasses = defaults.split(' ')
  const overrideClasses = overrides.split(' ')
  const allClasses = [...defaultClasses, ...overrideClasses]

  const clashes = Object.keys(classesByFamily).reduce((acc, k) => {
    // if family classes intersect allClasses length > 1, we have specificity issues
    const intersection = classesByFamily[k].filter(x => allClasses.includes(x))
    if (intersection.length > 1) {
      return [...acc, k]
    } else {
      return acc
    }
  }, [])

  if (clashes.length > 0) {
    const mergedClasses = defaultClasses
      .reduce((acc, cls) => {
        const foundClash = clashes.find(f => classesByFamily[f].includes(cls))
        return !!foundClash ? acc : [...acc, cls]
      }, [])
      .concat(overrideClasses)
    return mergedClasses.join(' ')
  } else {
    return allClasses.join(' ')
  }
}

export const Cell = ({ className, noPadding = false, last = false, ...restProps }) => (
  <td
    className={classNames([
      tachyonsMerge('pv3', className),
      !noPadding && 'ph3',
      className
    ])}
    {...restProps}
  />
)

export class Table extends React.Component {
  static Header = TableHeader
  static Th = HeaderCell
  static Body = TableBody
  static Row = Row
  static Cell = Cell

  render () {
    const { className, children } = this.props
    return (
      <table className={classNames(['table-collapse br2 pv2 ph3 w-100', className])}>
        {children}
      </table>
    )
  }
}

export const LinkButton = (
  { onClick, children, disabled = false, className }
) => (
  <button
    className={tachyonsMerge(classNames([
      'di pa0 bg-transparent bn link underline blue',
      disabled ? 'o-40 not-allowed' : 'pointer'
    ]), className)}
    type='button'
    onClick={onClick}
    disabled={disabled}
  >
    {children}
  </button>
)

export const Link = (
  { className, ...props }
) => (
  <RRLink
    className={tachyonsMerge('link underline pointer blue', className)}
    {...props}
  />
)

export const Tier3Nav = ({ children }) => (
  <ul className='list ph0 mr4 mv0'>{children}</ul>
)

export const T3NavItem = ({ to, name, exact = false }) => {
  const activeClasses = 'b--primary-color bg-near-white' // : 'b--white'
  const inactiveClasses = 'b--white'

  return (
    <li className='mb2'>
      <RRLink
        getProps={({ isCurrent, isPartiallyCurrent }) => {
          const className = classNames([
            'ph3 pv2 db bl bw2 black no-underline underline-hover',
            (exact ? isCurrent : isPartiallyCurrent) ? activeClasses : inactiveClasses
          ])
          return { className }
        }}
        to={to}
      >
        {name}
      </RRLink>
    </li>
  )
}

export const Code = props => <code className='f7 pa1 bg-near-white' {...props} />

export const Badge = ({ type = 'success', ...restProps }) => {
  const classes = classNames({
    'ph2 pv1 bw1 ba br-pill': true,
    'bg-washed-green green': type === 'success',
    'bg-washed-error-color error-color': type === 'error'
  })
  return <span className={classes} {...restProps} />
}

export const Tag = ({ className = 'bg-near-white', style = null, children }) => (
  <span className={classNames(['dib ph2 pv1 mr3', className])} style={style}>
    {children}
  </span>
)
