import React from 'react'
import isEmpty from 'lodash/isEmpty'

import ModalDialog from 'common/components/modal-dialog'
import { LinkButton, Link } from 'common/components'
import PageHeading from 'common/components/page-heading'
import Button from 'common/components/button'

import { TaskAction } from '../index'

import useAnnotationState from '../use-annotation-state'
import Annotator from './annotator'
import DocumentControls from './document-controls'
import AnnotationInstructions from './annotation-instructions'
import TaskProgressBar from './task-progress-bar'
import validateTask from './validate-task'

interface Project {
  name: string
  fragmentCategories: any
}

interface AnnotatorPageProps {
  project: Project
  task: any
  document: any
  createLabel(any): any
  createReviewLabel(any): any
  onSkipTask(): any
  sidebarTop(): any
  onClose?: any | null
}

const assertUnreachable = (x: never): never => {
  throw new Error(`Should never reach this: ${x}`)
}

export const LabelInterface = ({
  project,
  task,
  document,
  onSkipTask,
  createLabel,
  createReviewLabel,
  sidebarTop: SidebarTop = () => null,
  onClose = null
}: AnnotatorPageProps) => {
  const taskAction: TaskAction = task.action
  const initialAnnotations = task => {
    if (taskAction === TaskAction.REVIEW) {
      return task.externalLabel ? task.externalLabel.contents.annotations : []
    } else {
      return task.label ? task.label.annotations : []
    }
  }

  const {
    annotations,
    hasChanges,
    addAnnotation,
    removeAnnotation,
    setAnnotations,
    resetAnnotations
  } = useAnnotationState(initialAnnotations(task))
  const [error, setError] = React.useState(null)

  // @ts-ignore
  React.useEffect(() => {
    setAnnotations(initialAnnotations(task))
  }, [task])

  const handleSkipTask = () => {
    onSkipTask()
    resetInterface()
  }

  const resetInterface = () => {
    setError(null)
    resetAnnotations()
  }

  const handleSaveLabel = async values => {
    const { documentClass: categoryId } = values
    const [isValid, validationError] = validateTask(task, categoryId, annotations)
    if (!isValid) {
      setError(validationError)
      return
    }

    switch (taskAction) {
      case TaskAction.CREATE:
        createLabel({
          documentId: document.id,
          contents: {
            categoryId,
            annotations: isEmpty(annotations) ? null : annotations
          }
        })
        break
      case TaskAction.REVIEW:
        createReviewLabel({
          documentId: document.id,
          contents: {
            result: hasChanges ? 'modify' : 'accept',
            categoryId,
            annotations
          }
        })
        break
      // case TaskAction.VIEW:
      //   // no-op
      //   break
      default:
        return assertUnreachable(taskAction)
    }

    resetInterface()
  }

  const handleRejectLabel = async () => {
    createReviewLabel({
      documentId: document.id,
      contents: { result: 'reject' }
    })
    resetInterface()
  }

  // NOTE: use relative font size helper classes for jupyter widget, as jupyter resets root font-size to 10px
  return (
    <div
      className='flex bg-off-white b--light-gray ba pa3'
      style={{ fontSize: 16 }}
      data-cy='widget'
    >
      <div className='flex-2'>
        <Annotator
          annotations={annotations}
          categories={project.fragmentCategories.nodes}
          onAddAnnotation={addAnnotation}
          onRemoveAnnotation={removeAnnotation}
          readOnly={taskAction !== TaskAction.CREATE}
        >
          {document.contents}
        </Annotator>
      </div>

      {/* TODO-3: make this max-height ~400 and position sticky */}
      <div className='flex-1 flex flex-column ml4' data-cy='widget--annotator-controls'>
        <div className='flex-1'>
          <div className='bg-white b--light-gray ba pa3 pb4 shadow'>
            <SidebarTop />
            <DocumentControls
              project={project}
              task={task}
              labels={annotations}
              error={error}
              onSkip={handleSkipTask}
              onSave={handleSaveLabel}
              hasChanges={hasChanges as boolean}
              resetChanges={resetAnnotations}
              onReject={handleRejectLabel}
            />
          </div>
        </div>
        {onClose && (
          <div className='flex-0 mt5 tr'>
            <Button level='secondary' className='inverted' onClick={_e => onClose()}>
              Exit Labeling
            </Button>
          </div>
        )}
      </div>
    </div>
  )
}

const LabelInterfaceWrapper = props => {
  const { project, task, progress } = props
  const [areInstructionsVisible, setInstructionsVisible] = React.useState(false)
  const taskAction: TaskAction = task.action

  return (
    <React.Fragment>
      <PageHeading
        title={
          taskAction === TaskAction.CREATE
            ? <React.Fragment>Labeling: <Link to='..' className='black no-underline underline-hover'>{project.name}</Link></React.Fragment>
            : <React.Fragment>Reviewing: <Link to='..' className='black no-underline underline-hover'>{project.name}</Link></React.Fragment>
        }
        noBorder
        vAlign={'center'}
        subhead={
          <LinkButton onClick={() => setInstructionsVisible(true)}>
            View instructions
          </LinkButton>
        }
      >
        <TaskProgressBar
          className='w-33'
          complete={progress.complete + progress.skipped}
          total={progress.total}
        />
      </PageHeading>

      <LabelInterface {...props} />

      <ModalDialog
        isOpen={areInstructionsVisible}
        onRequestClose={() => setInstructionsVisible(false)}
      >
        <AnnotationInstructions
          project={project}
          onClose={() => setInstructionsVisible(false)}
        />
      </ModalDialog>
    </React.Fragment>
  )
}

export default LabelInterfaceWrapper
