import React from 'react'
import classNames from 'classnames'

const PageHeading = ({
  title,
  children = null,
  subhead = null,
  vAlign = 'baseline',
  noBorder = false
}) => {
  const classes = classNames([
    'flex flex-row justify-between space-between mb4',
    `items-${vAlign}`,
    !noBorder && 'bb b--light-silver'
  ])

  const Title = typeof title === 'function' ? title : () => <React.Fragment>{title}</React.Fragment>

  return (
    <div className={classes}>
      <div className='flex-1'>
        <h1 className='mb1 lh-title'>
          <Title />
        </h1>
        {subhead && <div>{subhead}</div>}
      </div>
      {children}
    </div>
  )
}

export default PageHeading
