import React from 'react'
import { Formik, Form } from 'formik'

import { allowModifyOfReviewLabels } from '../../feature-flags'

import Button from 'common/components/button'
import { LinkButton } from 'common/components'
import { FormError } from 'common/components/form'

import { TaskAction } from '../index'

import FragmentControls from './fragment-controls'
import ClassificationControls from './classification-controls'

interface DocumentControlsProps {
  project: any
  task: any
  labels: any
  onSkip(any): any
  onSave(any): any
  onReject(): any
  hasChanges: boolean
  resetChanges: any
  error: any
}

const DocumentControls = ({
  project,
  task,
  error,
  labels,
  onSkip: handleSkip,
  onSave,
  onReject: handleReject,
  hasChanges = false,
  resetChanges = () => {}
}: DocumentControlsProps) => {
  return (
    <Formik
      initialValues={{
        documentClass: task.label
          ? task.label.categoryId
          : task.externalLabel
          ? task.externalLabel.contents.categoryId
          : ''
      }}
      onSubmit={async (values, { setSubmitting, setErrors, resetForm }) => {
        setSubmitting(true)
        try {
          await onSave(values)
          resetForm()
        } catch (err) {
          setErrors(err.errors)
          setSubmitting(false)
        }
      }}
      enableReinitialize={true}
      render={({ values, handleChange, resetForm, dirty }) => (
        <Form>
          {error && <FormError error={error} />}

          {task.type.includes('classification') && (
            <ClassificationControls
              project={project}
              action={task.action}
              values={values}
              onChange={handleChange}
            />
          )}

          {task.type.includes('fragment') && (
            <FragmentControls project={project} action={task.action} labels={labels} />
          )}

          {task.action === TaskAction.REVIEW ? (
            <ReviewControlButtons
              onReject={handleReject}
              onSkip={handleSkip}
              hasChanges={allowModifyOfReviewLabels && (hasChanges || dirty)}
              onReset={() => {
                resetForm() // reset document classification
                resetChanges() // reset annotations
              }}
            />
          ) : (
            <CreateControlButtons onReset={resetForm} onSkip={handleSkip} />
          )}
        </Form>
      )}
    />
  )
}

const ReviewControlButtons = ({ hasChanges = false, onReject, onReset, onSkip }) => (
  <React.Fragment>
    <div className='flex justify-between'>
      <div>
        <Button type='submit' className='mr2'>
          {allowModifyOfReviewLabels || hasChanges ? 'Modify' : 'Accept'}
        </Button>
      </div>

      <div>
        <Button
          type='button'
          onClick={onReject}
          className='mr2'
          level='secondary'
          disabled={hasChanges}
        >
          Reject
        </Button>
      </div>

      <div>
        <Button
          type='button'
          className='inverted'
          onClick={() => {
            onReset()
            onSkip()
          }}
        >
          Skip
        </Button>
      </div>
    </div>

    {allowModifyOfReviewLabels && (
      <div className='mt4'>
        <LinkButton onClick={onReset} disabled={!hasChanges}>
          Reset changes
        </LinkButton>
      </div>
    )}
  </React.Fragment>
)

// TODO-3: is onReset still needed?
const CreateControlButtons = ({ onReset, onSkip }) => (
  <div className='flex space-between justify-center'>
    <div className='flex-3'>
      <Button type='submit' className='w4'>
        Save
      </Button>
    </div>
    <div className='flex-1'>
      <Button
        type='button'
        className='inverted'
        onClick={() => {
          onReset()
          onSkip()
        }}
      >
        Skip
      </Button>
    </div>
  </div>
)

export default DocumentControls
