import React from 'react'
import uuid from 'uuid/v4'
import isNaN from 'lodash/isNaN'

import AnnotatedElement from './annotated-element'
import { ELEMENT_MAP } from './utils'
import {
  transformContents,
  transformApiAnnotationToComponentAnnotation,
  transformComponentAnnotationToApiAnnotation,
  TransformedContents,
  ComponentAnnotation,
  Annotation
} from './transforms'
import './annotator.css'

interface AnnotatorProps {
  annotations: Annotation[]
  categories: any
  onRemoveAnnotation?: any
  readOnly?: boolean
  options?: any
  onAddAnnotation?: any
  children: any
}

const Annotator = ({
  annotations: annotationsFromProps = [],
  categories,
  onAddAnnotation,
  onRemoveAnnotation,
  readOnly,
  options: overrideOptions,
  children
}: AnnotatorProps) => {
  const [
    pendingAnnotation,
    setPendingAnnotation
  ] = React.useState<ComponentAnnotation | null>(null)

  // TODO-2: memoize this
  const annotations = annotationsFromProps.map(a =>
    transformApiAnnotationToComponentAnnotation(children, a)
  )

  const handleRemoveAnnotation = (annotation: ComponentAnnotation) => {
    const toRemove = transformComponentAnnotationToApiAnnotation(children, annotation)
    onRemoveAnnotation(toRemove)
  }

  const defaultOptions = { snapToWordBoundaries: false }
  const options = { ...defaultOptions, ...overrideOptions }

  const renderContent = (contents: string | TransformedContents[]) => {
    const allAnnotations = pendingAnnotation
      ? annotations.concat(pendingAnnotation)
      : annotations

    return typeof contents === 'string'
      ? contents
      : contents.map(({ type, id, value }, key) => {
          const annotationsForElement = allAnnotations.filter(
            a => a.selector.elementId === id
          )

          const Tag = ELEMENT_MAP[type] || ELEMENT_MAP['unknown']

          return annotationsForElement.length > 0 ? (
            <AnnotatedElement
              key={id}
              id={id}
              type={type}
              value={value}
              annotations={annotationsForElement}
              onRemoveAnnotation={handleRemoveAnnotation}
              onCancelAnnotation={() => setPendingAnnotation(null)}
              onAddAnnotation={addAnnotation}
              categories={categories}
              readOnly={readOnly}
            />
          ) : (
            <Tag key={id} id={id}>
              {renderContent(value)}
            </Tag>
          )
        })
  }

  const addAnnotation = ({ id: categoryId, notes }) => {
    onAddAnnotation({
      ...transformComponentAnnotationToApiAnnotation(children, pendingAnnotation),
      categoryId,
      notes
    })
    setPendingAnnotation(null)
  }

  const snapToWordBoundaries = sel => {
    let anchorOffset = sel.anchorOffset
    let focusOffset = sel.focusOffset
    let length = focusOffset - anchorOffset

    if (!options.snapToWordBoundaries) {
      return [anchorOffset, focusOffset]
    }

    const direction = length > 0 ? 1 : -1
    const directionOffset = length > 0 ? 0 : 1

    let trailingChar = sel.focusNode.textContent.charAt(focusOffset - directionOffset)
    while (
      trailingChar !== ' ' &&
      focusOffset > 0 &&
      focusOffset < sel.focusNode.textContent.length
    ) {
      focusOffset += direction * 1
      trailingChar = sel.focusNode.textContent.charAt(focusOffset - directionOffset)
    }
    length = focusOffset - anchorOffset

    let precedingChar = sel.anchorNode.textContent.charAt(anchorOffset - direction * 1)
    while (
      precedingChar !== ' ' &&
      anchorOffset > 0 &&
      anchorOffset < sel.focusNode.textContent.length
    ) {
      anchorOffset -= direction * 1
      precedingChar = sel.anchorNode.textContent.charAt(anchorOffset - direction * 1)
    }
    anchorOffset += directionOffset

    return [anchorOffset, focusOffset]
  }

  const handleMouseUp = () => {
    if (readOnly || categories.length === 0) {
      return
    }

    setPendingAnnotation(null)

    const sel = window.getSelection()
    if (sel.toString().length <= 0) {
      return
    }

    const [anchorOffset, focusOffset] = snapToWordBoundaries(sel)
    const length = focusOffset - anchorOffset

    const parent = sel.anchorNode.parentElement
    const elementId = parent.id

    const baseOffset = anchorOffset < focusOffset ? anchorOffset : focusOffset
    let nodeOffset = 0
    // NOTE: this assumes that selection is text nodes only
    // NOTE: using `find` to act as a do-until
    Array.prototype.find.call(parent.childNodes, (child: Text) => {
      if (child === sel.anchorNode) {
        return true
      } else {
        nodeOffset +=
          child instanceof Element
            ? child.childNodes[0]
              ? (child.childNodes[0] as Text).length
              : 0
            : child.length
        return false
      }
    })

    const offset = nodeOffset + baseOffset

    if (isNaN(offset) || length === 0) {
      return
    }

    // NOTE: it's necessary to pause a tick so that sel doesn't get reset to node inside the popup
    setTimeout(() => {
      setPendingAnnotation({
        id: uuid(),
        selectedText: sel.anchorNode.textContent.substr(anchorOffset, length),
        selector: {
          elementId,
          offset,
          length: length > 0 ? length : -length
        },
        categoryId: null
      })
    }, 0)
  }

  const transformedContents = transformContents(children)

  return (
    <div
      className='annotator'
      id='annotator'
      data-cy='annotator'
      onMouseUp={handleMouseUp}
    >
      {renderContent(transformedContents)}
    </div>
  )
}

export default Annotator
