import React from 'react'
import { useQuery, useMutation } from 'react-apollo-hooks'

import { parseQueryParams } from 'common/route-utils'

import LabelInterface from './components/label-interface'
import {
  NEXT_INCOMPLETE_TASK_QUERY,
  SKIP_TASK_MUTATION,
  CREATE_LABEL_MUTATION
} from './queries'

export enum TaskAction {
  CREATE = 'CREATE',
  REVIEW = 'REVIEW',
  // VIEW = 'VIEW'
}

const AnnotatorPage = ({ projectId, location, navigate, flash, ...props }) => {
  const query = parseQueryParams(location)
  const { data, loading, error } = useQuery(NEXT_INCOMPLETE_TASK_QUERY, {
    variables: { projectId, cycleId: query.cycle }
  })
  const createLabel = useMutation(CREATE_LABEL_MUTATION, {
    refetchQueries: [{ query: NEXT_INCOMPLETE_TASK_QUERY, variables: { projectId, cycleId: query.cycle } }]
  })
  const skipTask = useMutation(SKIP_TASK_MUTATION, {
    refetchQueries: [{ query: NEXT_INCOMPLETE_TASK_QUERY, variables: { projectId, cycleId: query.cycle } }]
  })

  if (loading) return <div>loading...</div>
  if (error) return <div>error: {error.message}</div>

  const project = data.projectById
  const task = data.nextIncompleteTask.nodes[0]
  const document = task ? task.document : null
  const progress = data.taskProgress

  if (!document) {
    navigate(`/projects/${projectId}`, {
      state: {
        flash: query.cycle ? `You have no tasks remaining for cycle: ${query.cycle}` : `You have no tasks remaining for this project.`
      }
    })
    return null
  }

  // TODO-3: remove currentUserProfile, instead use current_user_id() in a postgres function
  const { currentUserProfile } = data
  const { id: creatorId } = currentUserProfile
  const cycleId = task.cycleId

  const handleCreateLabel = async values => {
    await createLabel({
      variables: {
        projectId,
        cycleId,
        creatorId,
        taskId: task.id,
        ...values
      }
    })
    flash.setMessage('Label saved successfully.')
  }

  const handleCreateReviewLabel = async values => {
    const { documentId } = values

    const contents = {
      accept: { result: 'accept' },
      reject: { result: 'reject' },
      modify: values.contents
    }[values.contents.result]

    await createLabel({
      variables: {
        projectId,
        cycleId,
        creatorId,
        taskId: task.id,
        documentId,
        taskAction: 'review',
        parentLabelId: task.externalLabel.id,
        contents: contents
      }
    })
  }

  const handleSkipTask = async () => {
    await skipTask({ variables: { taskId: task.id } })
    flash.setMessage('Task skipped successfully.')
  }

  return (
    <LabelInterface
      {...props}
      project={project}
      task={task}
      document={document}
      progress={progress}
      onSkipTask={handleSkipTask}
      createLabel={handleCreateLabel}
      createReviewLabel={handleCreateReviewLabel}
    />
  )
}

export default AnnotatorPage
