import React from 'react'
import classNames from 'classnames'

import { Percentage } from 'project/components/completion-percentage'
import ProgressBar from 'common/components/progress-bar'

const TaskProgressBar = ({ className = null, compact = false, complete, total }) => {
  const renderSummary = () => (
    <div className='flex space-between'>
      <p className='f7-rel ma0'>
        {complete} complete, {total - complete} remaining
      </p>
      {compact && (
        <div className='flex-1 b tr f7-rel'>
          <Percentage count={complete} total={total} />
        </div>
      )}
    </div>
  )

  return (
    <div
      className={classNames([
        'bg-off-white b--light-gray ba pa2',
        className,
        compact && 'mb4'
      ])}
    >
      {!compact ? (
        <div className='flex space-between'>
          <div className='flex-1'>
            <h6 className='f6-rel ttu mid-gray ma0'>Progress</h6>
          </div>
          <div className='flex-1 b tr'>
            <Percentage count={complete} total={total} />
          </div>
        </div>
      ) : (
        renderSummary()
      )}
      <ProgressBar className='mv2' percent={(complete / total) * 100} />
      {!compact && renderSummary()}
    </div>
  )
}

export default TaskProgressBar
