import React from 'react'
import chroma from 'chroma-js'

import Button from 'common/components/button'

const AnnotationInstructions = ({ project, onClose: handleClose }) => (
  <React.Fragment>
    <h3 className='mt0 bb'>Annotation instructions</h3>
    <div dangerouslySetInnerHTML={{ __html: project.instructions }} />

    <h3 className='bb'>Document labels</h3>
    <dl>
      {project.documentCategories.nodes.map((category, index) => (
        <React.Fragment key={index}>
          <dt className='b dib'>{category.name}</dt>
          <dd className='mb2'>{category.description}</dd>
        </React.Fragment>
      ))}
    </dl>

    <h3 className='bb'>Annotation labels</h3>
    <dl>
      {project.fragmentCategories.nodes.map((category, index) => (
        <React.Fragment key={index}>
          <dt
            className='b dib ph2 pv1'
            style={{ backgroundColor: chroma(category.properties.color).brighten(0.2) }}
          >
            {category.name}
          </dt>
          <dd className='mb2'>{category.description}</dd>
        </React.Fragment>
      ))}
    </dl>

    <div className='tr'>
      <Button level='secondary' className='inverted' onClick={handleClose}>
        Close
      </Button>
    </div>
  </React.Fragment>
)

export default AnnotationInstructions
