import React from 'react';
import ReactDOM from 'react-dom';
import widgets from '@jupyter-widgets/base';

import App from './app.tsx'

export const AnnotationModel = widgets.DOMWidgetModel.extend({
  defaults: _.extend(widgets.DOMWidgetModel.prototype.defaults(), {
    _model_name : 'AnnotationModel',
    _view_name : 'AnnotationView',
    _model_module : 'markers',
    _view_module : 'markers',
    _model_module_version : '0.3.0',
    _view_module_version : '0.3.0',
    metadata: { apiKey: null, apiUrl: null, projectId: null, cycleId: null }
  })
});

export const AnnotationView = widgets.DOMWidgetView.extend({
  render: function() {
    this.syncView();
    this.model.on('change:document', this.syncView, this);
    this.model.on('change:metadata', this.syncView, this);

    // fixes styling issues, behind a timeout to allow jupyter to mount our view
    setTimeout(() => {
      // NOTE: this is so that multiple markers widgets can properly overflow
      // TODO-2: only call this on annotator widgets
      Array.prototype.forEach.call(document.querySelectorAll('.p-Widget'), e => e.style.overflow = 'visible')
      Array.prototype.forEach.call(document.querySelector('.jupyter-widgets-view'), e=> {
        e.style.overflow = 'visible';
        e.style.padding = '10px 0';
      })
    }, 0);
  },

  syncView: function() {
    const metadata = this.model.get('metadata')
    const handleClose = () => this.send({ event: 'close_widget' })

    ReactDOM.render((
      <App
        metadata={metadata}
        onClose={handleClose}
      />
    ), this.el);

    // to set model:
    // this.model.set('value', newValue)
    // to sync with python:
    // this.touch()
  }
});

export { version } from '../package.json'
